import React from 'react'
import json from './../../json/static.json'
import { useSelector } from 'react-redux'

const UserTable = () => {
  const { users } = useSelector((state) => state.users)
  return (
    <>
      <table className="table text-center">
        <thead>
          <tr>
            <th>{json.sn}</th>
            <th>{json['first name']}</th>
            <th>{json['last name']}</th>
            <th>{json.email}</th>
            <th>{json.role}</th>
            <th>{json['phone number']}</th>
          </tr>
        </thead>
        <tbody>
          {users?.map((item, i) => (
            <tr key={item.id}>
              <td>{i + 1}</td>
              <td>{item.first_name}</td>
              <td>{item.last_name}</td>
              <td>{item.email}</td>
              <td>
                <span
                  class={`badge ${
                    item.role === 'GUARD' ? 'bg-primary' : 'bg-success'
                  }`}
                >
                  {item.role}
                </span>
              </td>
              <td>{item.phone}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </>
  )
}

export default UserTable
